.register{
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(
        rgba(255, 255, 255, 0.5),
        rgba(255, 255, 255, 0.5)
    ),
     url(/public/image/train-8517089_1280.jpg);
    background-size: cover;
}

.registertitle{
    font-size: 50px;
}

.registerform{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}

.registerform > label {
    margin: 10px 0;
}

.registerinput{
    padding: 10px;
    background-color: white;
    border: none;
}

.registerbutton{
    margin-top: 20px;
    cursor: pointer;
    background-color: teal;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;
}

.registerloginbutton{
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: lightcoral;
    cursor: pointer;
    border: none;
    padding: 10px;
    color: white;
    border-radius: 10px;
}